import React from 'react';
import { Helmet } from 'react-helmet';
import { useLocation } from "react-router-dom";
import { Link, Outlet } from "react-router-dom";
import Giscus from "@giscus/react"

export const Nzk1 = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');
    const klm = document.getElementById('kolom-komen');
    const btn = document.getElementById('btn-komen');
    const cls = document.getElementById('close-komen');

    function open() {
        klm.style.display = 'block';
        btn.style.display = 'none';
        cls.style.display = 'flex';
    };
    function close() {
        klm.style.display = 'none';
        cls.style.display = 'none';
        btn.style.display = 'block';
    };

    return (
        <div>
            <Helmet>
                <title>Chapter {(splitlocation[3])} - {(splitlocation[1])} - Riztranslation</title>
                <style>{`
                    img{
                        height: 100%;
                        width: 100%;
                    }

                `}</style>
            </Helmet>
            <a className='main-front'>
                <div className='first-front'>
                    <Link className='title-ch prev' to={ "/" + (splitlocation[1])+ "/" + (splitlocation[2]) + "/" + (splitlocation[3] * 1 - 1)}>◀</Link>
                    <Link className='title-ch judul' to={"/"+ (splitlocation[1]) }><p>{(splitlocation[1])}</p> Ch {(splitlocation[3])}</Link>
                    <Link className='title-ch next' to={ "/" + (splitlocation[1])+ "/" + (splitlocation[2]) + "/" + (splitlocation[3] * 1 + 1)}>▶</Link>
                    <Link onClick={open} id='btn-komen' className='title-ch komen-btn'>💬</Link>
                </div>
                <a onClick={close} id="close-komen" className='close-btn'><p>⨉</p></a>
                <div id='kolom-komen' className='second-front' style={{ zIndex: '5', animation: 'fadeIn 0.5s', backdropFilter: 'blur(10px)', paddingTop: '20px', padding: '10px', borderRadius: '3px', height: '85%', backgroundColor: 'rgb(197, 197, 197, 0.5)', margin: '5px', overflow: 'scroll', display: 'none' }}>
                    <Giscus style={{ animation: 'fadeIn 0.5s', color: 'inherit' }}
                        id="comments"
                        repo="Riztranslation/tgmkch1"
                        repoId="R_kgDOIb5w5A"
                        category="General"
                        categoryId="DIC_kwDOIb5w5M4CSj31"
                        mapping="title"
                        term="Silahkan komen!"
                        reactionsEnabled="1"
                        emitMetadata="1"
                        inputPosition="top"
                        theme="light"
                        lang="id"
                        loading="lazy"
                    />
                </div>
            </a>
            <div className='main-scroll'>
                <div id='atas'></div>
                <a href="https://imgbox.com/EqECbFfy" target="_blank"><img src="https://images2.imgbox.com/50/4b/EqECbFfy_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/KdzNdt9l" target="_blank"><img src="https://images2.imgbox.com/24/b3/KdzNdt9l_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/H2St90bL" target="_blank"><img src="https://images2.imgbox.com/21/60/H2St90bL_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/kb66oilX" target="_blank"><img src="https://images2.imgbox.com/f7/5e/kb66oilX_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/foqTa0r3" target="_blank"><img src="https://images2.imgbox.com/a5/03/foqTa0r3_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/AZWfNtwN" target="_blank"><img src="https://images2.imgbox.com/26/cc/AZWfNtwN_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/m5cH7msX" target="_blank"><img src="https://images2.imgbox.com/af/79/m5cH7msX_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/aD21QCGC" target="_blank"><img src="https://images2.imgbox.com/85/fe/aD21QCGC_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/vZhHiQlJ" target="_blank"><img src="https://images2.imgbox.com/08/e0/vZhHiQlJ_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/t4KD1vRW" target="_blank"><img src="https://images2.imgbox.com/69/9c/t4KD1vRW_o.jpg" alt="image host" /></a> <a href="https://imgbox.com/7nhYjwN4" target="_blank"><img src="https://images2.imgbox.com/9b/6e/7nhYjwN4_o.jpg" alt="image host" /></a>
            </div>
        </div>
    );
}