import './App.css';
import Home from './home';
import Web from './list-manga/Web';
import Serial from './list-manga/Serial';
import Nav from './komponen/nav';
import Tagahumika from './Manga/Tagahumika/index-ch';
import TgmkChapter from './Manga/Tagahumika/komponen/chapter';
import TgmkKomen from './Manga/Tagahumika/komponen/komen';
import TgmkTerkait from './Manga/Tagahumika/komponen/terkait';
import Nozokimi from './Manga/Nozokimi/index-ch';
import NzkChapter from './Manga/Nozokimi/komponen/chapter';
import NzkKomen from './Manga/Nozokimi/komponen/komen';
import NzkTerkait from './Manga/Nozokimi/komponen/terkait';
import Htb from './Manga/Hina-to-bambi/index-ch';
import HtbChapter from './Manga/Hina-to-bambi/komponen/chapter';
import HtbKomen from './Manga/Hina-to-bambi/komponen/komen';
import HtbTerkait from './Manga/Hina-to-bambi/komponen/terkait';
import { HashRouter, Route, Routes, Navigate } from 'react-router-dom';
import { Chapisi1, Chapisi10, Chapisi11, Chapisi12, Chapisi2, Chapisi3, Chapisi4, Chapisi5, Chapisi6, Chapisi7, Chapisi8, Chapisi9 } from './Manga/Tagahumika/komponen/chKomponen/chapter-isi';
import { Nzk1 } from './Manga/Nozokimi/komponen/chKomponen/chapter-isi';
import { Htb1 } from './Manga/Hina-to-bambi/komponen/chKomponen/chapter-isi';
import { Eror } from "./komponen/eror";
import {useLocation} from "react-router-dom";

export const App = () => {
  return (
    <div>
    <HashRouter>
      <div className='container-bg'></div>
      <Nav />
      <Routes>
        <Route path='/' element={<Home />}>
          <Route index element={<Serial />} />
          <Route index path='serial' element={<Serial />} />
          <Route path='Web' element={<Web />} />
        </Route>
        <Route path='/tagahumika' element={<Tagahumika />}>
          <Route index element={<TgmkChapter />} />
          <Route path='komen' element={<TgmkKomen />} />
          <Route path='terkait' element={<TgmkTerkait />} />
        </Route>
        <Route path='/tagahumika/chapter'>
          <Route path='1' element={<Chapisi1 />} />
          <Route path='2' element={<Chapisi2 />} />
          <Route path='3' element={<Chapisi3 />} />
          <Route path='4' element={<Chapisi4 />} />
          <Route path='5' element={<Chapisi5 />} />
          <Route path='6' element={<Chapisi6 />} />
          <Route path='7' element={<Chapisi7 />} />
          <Route path='8' element={<Chapisi8 />} />
          <Route path='9' element={<Chapisi9 />} />
          <Route path='10' element={<Chapisi10 />} />
          <Route path='11' element={<Chapisi11 />} />
          <Route path='12' element={<Chapisi12 />} />
        </Route>
        <Route path='/nozokimi' element={<Nozokimi />}>
          <Route index element={<NzkChapter />} />
          <Route path='komen' element={<NzkKomen />} />
          <Route path='terkait' element={<NzkTerkait />} />
        </Route>
        <Route path='/Nozokimi/chapter'>
          <Route path='1' element={<Nzk1 />} />
        </Route>
        <Route path='/hina-to-bambi' element={<Htb />}>
          <Route index element={<HtbChapter />} />
          <Route path='komen' element={<HtbKomen />} />
          <Route path='terkait' element={<HtbTerkait />} />
        </Route>
        <Route path='/hina-to-bambi/chapter'>
          <Route path='1' element={<Htb1 />} />
        </Route>
        <Route path='*' element={<Navi />} />
      </Routes>
    </HashRouter>
    </div>
  );
}

export const Navi = () => {
const location = useLocation();
const { pathname } = location;
const splitlocation = pathname.split('/');

  return (
    <div>
      <Navigate to={splitlocation[2] === "chapter" ? splitlocation[1] : splitlocation[0] } />
    </div>
  );
}
