import TimeAgo from 'timeago-react';
import { Link, Outlet } from 'react-router-dom';
import '../page.css';


export const Ch1 = () => {
    return (
        <div>
            <div className='conCh'>
                <div className='sampulCh vol1'></div>
                <div>
                    <p><TimeAgo className='timeago'
                        datetime={'2022-11-11 18:020:08'}
                        locale='id_ID'
                    /></p>
                    <Link className='chapterLink' to="/tagahumika/chapter/1"><h3>Chapter 1</h3><div className='group'>Tagahumika</div></Link>
                </div>
            </div>
        </div>
    );
}
